.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 70px;
  min-height: 74px;
}

@media screen and (max-width: 950px) {
  .header {
    padding: 0 30px;
  }
}

@media screen and (max-width: 425px) {
  .header {
    padding: 0 14px;
  }
}
